<template>
  <div class="mine">
    <div class="inf">
      <div class="word">
        <div class="personal">
          <span class="name">{{$store.state.person.name}}</span>
        </div>
        <div v-for="item in person.orgInfo">
          <div class="job_position">{{item.namePath || ''}}{{item.organizationName}} - {{item.position}}</div>
        </div>
      </div>
    </div>
    <div class="link first_img">
      <div @click="goToPageInvoiceList()">
        <mt-cell title="发票" is-link></mt-cell>
      </div>
      <div @click="goToPagePersonInfo()">
        <mt-cell title="个人信息" is-link></mt-cell>
      </div>
    </div>
    <div @click="goToPageHelp()" class="mine_help first_img">
      <mt-cell title="帮助" is-link></mt-cell>
    </div>
    <BottomNav :select="selected"></BottomNav>
  </div>
</template>
<script>
  import BottomNav from '../public/bottom_nav.vue'
  import { mapActions } from 'vuex'
  import { loadData } from '../../common/utils'
  import _ from 'lodash'

  export default {
    name: 'Mine',
    components: {
      BottomNav
    },
    data () {
      return {
        selected: 'user',
        person: {},
        subLevel: '',
        progress: null,
        subExp: null
      }
    },
    async created () {
      await this.initData()
    },
    methods: {
      ...mapActions(['getPersonInfo', 'getOrganizationInfo']),
      goToPageHelp: function () {
        this.$router.push({
          name: 'Help',
          query: {}
        })
      },
      goToPagePersonInfo: function () {
        this.$router.push({
          path: 'PersonInfo',
          query: {}
        })
      },
      goToPageInvoiceList: function () {
        this.$router.push({
          path: 'InvoiceList',
          query: {}
        })
      },
      async initData () {
        await loadData(() => { return this.getPersonInfo(this.$store.state.person.personId) })
        await this.getOrganizationInfo(this.$store.state.person.personId)
        let person = JSON.parse(JSON.stringify(this.$store.state.person))
        _.forEach(person.orgInfo, function (value) {
          if (value.organizationNamePath) {
            let index = _.indexOf(value.organizationNamePath, '/')
            if (index !== -1) {
              value.namePath = value.organizationNamePath.slice(0, index) + ' - '
            }
          }
        })
        this.person = person
      }
    }
  }
</script>
<style scoped>
  .org, .job_position {
    line-height: 1.8;
  }

  .inf {
    padding: 4%;
    background: #fff;
    color: #333;
    box-sizing: border-box;
  }

  .inf::after {
    content: '';
    display: block;
    clear: both;
  }

  .portrait img {
    border-radius: 50%;
    width: 100%;
    border: 2px solid #ffffff;
    box-sizing: border-box;
  }

  .word {
    float: right;
    width: 100%;
    box-sizing: content-box;
  }

  .name {
    display: inline-block;
    line-height: 2.5rem;
    font-size: 20px;
    vertical-align: middle;
  }

  .level {
    display: inline-block;
    background: #26a2ff;
    color: #fff;
    font-size: 12px;
    padding: 1px 5px;
    border-radius: 4px;
    margin-left: 4px;
  }

  .progress_level {
    font-size: 14px;
    display: block;
    height: auto;
    overflow: hidden;
    color: #999999;
  }

  .job_position {
    color: #999;
    font-size: 14px;
  }

  .upgrade {
    color: #999;
    font-size: 14px;
    line-height: 1.8;
  }

  .mt-progress {
    height: 15px;
    line-height: 15px;
  }

  @media screen and (width: 320px) {
    .name {
      font-size: 16px;
      line-height: 2rem;
    }

    .upgrade {
      font-size: 12px;
    }
  }

  @media screen and (width: 768px) {
    .name {
      font-size: 25px;
      line-height: 5rem;
    }

    .upgrade {
      font-size: 18px;
      line-height: 2rem;
    }

    .level {
      font-size: 16px;
      padding: 0 6px;
    }
  }

  @media screen and (width: 1024px) {
    .name {
      font-size: 40px;
      line-height: 5rem;
    }

    .upgrade {
      font-size: 30px;
      line-height: 5rem;
    }

    .level {
      font-size: 18px;
      padding: 2px 8px;
    }
  }

  .link {
    margin-top: 10px;
  }

  .progress {
    background: #26a2ff;
    height: 6px;
    width: 80%;
    font-size: 12px;
    border-radius: 5px;
  }

  .bg {
    width: 100%;
    display: inline-block;
    background: #e2e2e2;
    height: 6px;
    line-height: 1rem;
    border-radius: 5px;
  }

  .mine_help {
    margin-top: 10px
  }
</style>



